import React, { cloneElement, Children } from 'react';
import PropTypes from 'prop-types';

/**
 * inject extra props to children from a global register
 */

const PropsInjector = ({children, name}) => {
    const _props = window._propsInjectorRegister?.[name] || {}

    const arrayChildren = Children.toArray(children);

    return (
        <>
            {arrayChildren.map((c) =>
                cloneElement(c, _props)
            )}
        </>
    );
}

PropsInjector.defaultProps = {};

PropsInjector.propTypes = {
    /**
     * The Id used to ident this component in Dash callbacks.
     */
    id: PropTypes.string,

    /**
     * The Name used to get props from the register.
     */
    name: PropTypes.string.isRequired,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,

    /**
     * children
     */
    children: PropTypes.node,
};

export default PropsInjector;
