import React, { useEffect } from 'react';
import PropTypes from 'prop-types';

/**
 * dispatch mound and unMount Event to document
 */


const LifecycleEvents = ({id, children}) => {
    useEffect(()=>{
        const e = new CustomEvent("LifecycleEventsDidMount", {
            detail: {id},
          });
        document.dispatchEvent(e);
        return function cleanup() {
            const e = new CustomEvent("LifecycleEventsDidUnMount", {
                detail: {id},
              });
            document.dispatchEvent(e);
        };
    }, [])

    return <>{children}</>
}

LifecycleEvents.defaultProps = {};

LifecycleEvents.propTypes = {
    /**
     * The ID used to identify this component in Dash callbacks.
     */
    id: PropTypes.string.isRequired,

    /**
     * Dash-assigned callback that should be called to report property changes
     * to Dash, to make them available for callbacks.
     */
    setProps: PropTypes.func,

    /**
     * children
     */
    children: PropTypes.node,
};

export default LifecycleEvents;
